<template>
  <div>
    <van-nav-bar title="首页"/>
    <section class="home-content">
    <van-swipe :autoplay="3000">
      <van-swipe-item v-for="(image, index) in swiperList" :key="index">
        <img v-lazy="image" width="375" height="200"/>
      </van-swipe-item>
    </van-swipe>

    <van-row class="home-tab">
      <van-col :span="24/homeTabList.length" align="center" v-for="(item,index) in homeTabList" :key="index" @click.native="goPage(item)">
        <van-row>
          <van-icon class="home-tab-icon" :name="item.icon" />
          </van-row>
        <van-row>
          <span>{{item.title}}</span>
          </van-row>
        </van-col>
    </van-row>

    <section>
      <div class="group-title">文章列表</div>
      <van-cell-group>
        <van-cell title="蜂蜜的功效和作用" label="如何鉴别真假" @click="$router.push({name:'article',query:{articleId:1}})" is-link/>
        <van-cell title="如何鉴别真假" label="如何鉴别真假" @click="$router.push({name:'article',query:{articleId:2}})"  is-link/>
        <van-cell title="蜂蜜的功效和作用" label="如何鉴别真假" @click="$router.push({name:'article',query:{articleId:1}})" is-link/>
        <van-cell title="如何鉴别真假" label="如何鉴别真假" @click="$router.push({name:'article',query:{articleId:2}})"  is-link/>
        <van-cell title="蜂蜜的功效和作用" label="如何鉴别真假" @click="$router.push({name:'article',query:{articleId:1}})" is-link/>
        <van-cell title="如何鉴别真假" label="如何鉴别真假" @click="$router.push({name:'article',query:{articleId:2}})"  is-link/>
        <van-cell title="蜂蜜的功效和作用" label="如何鉴别真假" @click="$router.push({name:'article',query:{articleId:1}})" is-link/>
        <van-cell title="如何鉴别真假" label="如何鉴别真假" @click="$router.push({name:'article',query:{articleId:2}})"  is-link/>
      </van-cell-group>
      <!-- <img v-for="(item,index) in imgList" width="375" :key="index" v-lazy="item" alt="内容图"> -->
    </section>
    </section>
  </div>
</template>
<script>
import { NavBar, Swipe, SwipeItem, Icon, Cell, CellGroup } from "vant";
export default {
  components: {
    [NavBar.name]: NavBar,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Icon.name]: Icon,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup
  },
  data() {
    return {
      swiperList: [
        require("../assets/images/banner1.jpg"),
        require("../assets/images/banner2.jpg"),
        require("../assets/images/banner3.jpg")
      ],
      homeTabList: [
        {
          title: "蜂蜜功效",
          icon: "points",
          link: "article",
          articleId: 1
        },
        {
          title: "鉴别真假",
          icon: "password-view",
          link: "article",
          articleId: 2
        },
        {
          title: "蜂蜜产品",
          icon: "shop",
          link: "product-list"
        },
        {
          title: "联系我们",
          icon: "chat",
          link: "contact"
        }
      ],
      imgList: [
        require("../assets/images/image1.jpg"),
        require("../assets/images/image2.jpg"),
        require("../assets/images/image3.jpg")
      ]
    };
  },
  methods: {
    goPage(item) {
      if (item.link === "article") {
        this.$router.push({
          name: item.link,
          query: { articleId: item.articleId }
        });
      } else {
        this.$router.push({ name: item.link });
      }
    }
  }
};
</script>
<style scoped>
.home-content{
  height: calc(100% - 50px);
  overflow:auto;
}
.home-tab {
  margin: 10px 0;
}
.home-tab-icon {
  font-size: 30px;
}
.group-title {
  font-weight: 400;
  font-size: 14px;
  color: rgba(69, 90, 100, 0.6);
  padding: 10px 15px 15px;
}
</style>



